<template>
  <div class="stores">
    <Navbar></Navbar>
    <div class="taiwan">
      <div class="content">
        <svg width="350" height="600">
          <g class="counties"></g>
          <path class="county-borders"></path>
        </svg>
      </div>
      <div class="content content_second">
        <div class="message">
          <h1>{{ location }}</h1>
          <h1>{{ h2 }}</h1>
        </div>
        <div class="slider" v-if="show === 1">
          <splide :options="options">
            <splide-slide>
              <div class="col50">
                <div class="imgBx"><img :src="src" /></div>
              </div>

              <p class="titleText">{{ name }}</p>
            </splide-slide>
            <splide-slide>
              <div class="col50">
                <div class="imgBx"><img :src="src1" /></div>
              </div>

              <p class="titleText">{{ name1 }}</p>
            </splide-slide>
            <splide-slide>
              <div class="col50">
                <div class="imgBx"><img :src="src2" /></div>
              </div>
              <p class="titleText">{{ name2 }}</p>
            </splide-slide>
          </splide>
        </div>
        <div class="tableContain" v-if="showDiv === false">
          <div class="tableContaininner" v-for="(item, index) in day" :key="index">
            <div class="cloudy" v-if="item.weatherLike === '多雲'"></div>
            <div class="sunny" v-if="item.weatherLike === '晴時多雲'"></div>
            <div
              class="rainy"
              v-if="
                item.weatherLike === '陰短暫雨' ||
                item.weatherLike === '多雲短暫雨' ||
                item.weatherLike === '陰時多雲短暫雨' ||
                item.weatherLike === '多雲時陰短暫雨' ||
                item.weatherLike === '陰短暫陣雨' ||
                item.weatherLike === '多雲短暫陣雨' ||
                item.weatherLike === '陰時多雲短暫陣雨' ||
                item.weatherLike === '多雲短暫陣雨或雷雨' ||
                item.weatherLike === '陰短暫陣雨或雷雨'
              "
            ></div>
            <div class="cloudy sunCloudy" v-if="item.weatherLike === '多雲時晴'"></div>
            <div
              class="cloudy blackCloudy"
              v-if="
                item.weatherLike === '陰天' ||
                item.weatherLike === '陰時多雲' ||
                item.weatherLike === '多雲時陰'
              "
            ></div>
            <div class="weatherInf">
              <p>{{ item.startTime }}</p>
              <p>{{ item.minT }}&deg; ~ {{ item.maxT }}&deg;</p>
              <p>{{ item.weatherLike }} {{ item.poph }}%</p>
            </div>
          </div>
        </div>
      </div>
    </div>
    <button class="chageButton" v-if="show === 1" @click="getWeatherApi()"></button>
    <button class="goBackButton" v-if="show === 2" @click="goBack()"></button>
    <div class="island" @click="otherIslandFu(1)"></div>
    <div class="island island_other" @click="otherIslandFu(2)"></div>
  </div>
</template>

<script>
import error from "../assets/img/noImage.png";
import "@splidejs/splide/dist/css/themes/splide-default.min.css";
import * as d3 from "d3";
import * as topojson from "topojson";
import axios from "axios";
import data from "../assets/json/COUNTY_MOI_1090820.json";
import travelData from "../assets/json/travel.json";
import { Splide, SplideSlide } from "@splidejs/vue-splide";
export default {
  components: {
    Splide,
    SplideSlide,
  },
  data() {
    return {
      options: {
        type: "loop",
        rewind: true,
        width: 600,
        height: 350,
        perPage: 1,
        perMove: 1,
      },
      show: 0,
      country: "",
      location: "點擊地圖",
      h2: "",
      travel: [],
      src: "",
      src1: "",
      src2: "",
      name: "",
      name1: "",
      name2: "",
      noImage: error,
      day: [],
      showDiv: true,
    };
  },
  methods: {
    getCountries() {
      // console.log(data);
      this.taiwanCountry = data;
      this.draw(this.taiwanCountry);
    },
    draw(mapData) {
      let projection = d3.geoMercator().center([123.3, 24]).scale(7200);
      let path = d3.geoPath(projection);
      let data = mapData.objects["COUNTY_MOI_1090820"];
      let country = [];
      let strContryid = "";
      let arr = [];

      // console.log(`123` + obj);

      // console.log(JSON.stringify(data))
      // console.log(JSON.stringify(data));
      let data2 = JSON.stringify(data);
      // console.log(JSON.parse(data2).geometries);
      let data3 = JSON.parse(data2).geometries;
      for (let i = 0; i < data3.length; i++) {
        // console.log(data3[i].properties.COUNTYNAME)
        country.push(data3[i].properties.COUNTYNAME);
      }
      // console.log(country);
      this.taiwanCountry = country;
      // console.log(this.taiwanCountry[0]);
      d3.select("g.counties")
        .selectAll("path")
        .data(topojson.feature(mapData, mapData.objects["COUNTY_MOI_1090820"]).features)
        .enter()
        .append("path")
        .attr("d", path)
        .attr("id", (d) => d.properties.COUNTYNAME + "," + d.properties.COUNTYENG)
        // .attr("eng", (d) => d.properties.COUNTYENG)
        // .attr("class", (d) => d.properties.COUNTYENG)
        .on("click", (d) => {
          // console.log(d.target.id)
          strContryid = d.target.id;
          arr = strContryid.split(",");
          this.location = arr[0];
          this.h2 = arr[1];
          this.getJson(arr[0]);
          this.showDiv = true;
          this.day = [];
        });
    },
    getJson(e) {
      // let region = travelData.XML_Head.Infos.Info[1].Region;
      // console.log(region);

      // console.log(e);
      let arr = [];
      let Infos = travelData.XML_Head.Infos.Info;
      for (let i = 0; i < travelData.XML_Head.Infos.Info.length; i++) {
        if (e === travelData.XML_Head.Infos.Info[i].Region) {
          arr.push(Infos[i]);
        }
      }
      // console.log(JSON.stringify(arr[0].Name));
      // console.log(JSON.stringify(this.getRandomArrayElements(arr, 3)));

      // this.travel = arr;
      this.travel = this.getRandomArrayElements(arr, 3);
      // console.log(this.travel.length);
      // console.log(JSON.stringify(this.travel));

      console.log(JSON.stringify(this.travel[0].Picture1));
      console.log(JSON.stringify(this.travel[1].Picture1));
      console.log(JSON.stringify(this.travel[2].Picture1));
      if (this.travel[0].Picture1 === "" || this.travel[0].Picture1 === null) {
        this.travel[0].Picture1 = this.noImage;
      }
      if (this.travel[1].Picture1 === "" || this.travel[1].Picture1 === null) {
        this.travel[1].Picture1 = this.noImage;
      }
      if (this.travel[2].Picture1 === "" || this.travel[2].Picture1 === null) {
        this.travel[2].Picture1 = this.noImage;
      }
      this.src = this.travel[0].Picture1;
      this.src1 = this.travel[1].Picture1;
      this.src2 = this.travel[2].Picture1;
      this.name = this.travel[0].Name;
      this.name1 = this.travel[1].Name;
      this.name2 = this.travel[2].Name;
      this.show = 1;
    },
    getRandomArrayElements(arr, count) {
      let shuffled = arr.slice(0),
        i = arr.length,
        min = i - count,
        temp,
        index;
      while (i-- > min) {
        index = Math.floor((i + 1) * Math.random());
        temp = shuffled[index];
        shuffled[index] = shuffled[i];
        shuffled[i] = temp;
      }
      return shuffled.slice(min);
    },
    getWeatherApi() {
      let location = this.location;
      // console.log(this.location)
      this.showDiv = false;
      this.show = 2;
      let url =
        "https://opendata.cwb.gov.tw/api/v1/rest/datastore/F-D0047-091?Authorization=CWB-120DDC05-B795-47D5-BD36-345F74319252&elementName=MinT,MaxT,Wx,PoP12h";
      axios
        .get(url)
        .then((res) => {
          let locations = res.data.records.locations[0].location;
          // console.log(locations);
          let getLocation = locations.filter(function (value) {
            // console.log(value);
            // console.log(location)
            return value.locationName.indexOf(location) >= 0;
          });
          // console.log(getLocation[0].weatherElement[1]);
          let pop12 = getLocation[0].weatherElement[0];
          let wx = getLocation[0].weatherElement[1];
          let minT = getLocation[0].weatherElement[2];
          let maxT = getLocation[0].weatherElement[3];
          // console.log(JSON.stringify(pop12));
          // console.log(JSON.stringify(minT.time[0].elementValue[0].value));
          // console.log(JSON.stringify(maxT.time));
          for (let i = 0; i < wx.time.length; i++) {
            // console.log(minT.time[i].startTime.slice(11, 16))
            if (i > 0) {
              if (minT.time[i].startTime.slice(11, 16) !== "06:00") {
                continue;
              }
            }
            if (minT.time[i].startTime.slice(11, 16) === "00:00") {
              continue;
            }
            let poph = pop12.time[i].elementValue[0].value;
            if (poph === " ") {
              poph = "N/A";
            }
            let obj = {
              startTime: minT.time[i].startTime.slice(5, 10),
              // endTime: maxT.time[i].endTime.slice(5, 10),
              weatherLike: wx.time[i].elementValue[0].value,
              minT: minT.time[i].elementValue[0].value,
              maxT: maxT.time[i].elementValue[0].value,
              poph: poph,
            };
            this.day.push(obj);
          }
          // console.log(JSON.stringify(this.day));
        })
        .catch((error) => {
          console.error(error);
        });
    },
    goBack() {
      this.show = 1;
      this.showDiv = true;
      this.day = [];
    },
    otherIslandFu(e) {
      console.log(e);
      let obj = {};
      obj = {
        location: "連江縣",
        locationEn: "Lienchiang County",
      };
      if (e === 1) {
        obj = {
          location: "金門縣",
          locationEn: "Kinmen County",
        };
      }

      this.location = obj.location;
      this.h2 = obj.locationEn;
      this.getJson(this.location);
      this.showDiv = true;
      this.day = [];
    },
  },
  mounted() {
    this.getCountries();
  },
};
</script>
<style scoped>
.taiwan {
  flex-wrap: wrap;
  display: flex;
  margin: 0px auto;
  align-items: center;
  max-width: 1440px;
}
.counties {
  fill: #063061;
}

.counties :hover {
  fill: #05a6e0cc;
  transition: 0.5s;
}

.county-borders {
  fill: none;
  stroke: #fff;
  stroke-width: 0.5px;
}
.island {
  background-image: url("../assets/img/taiwan_island1.png");
  background-position: 55% 40%;
  background-size: 250%;
  width: 100px;
  height: 100px;
  position: absolute;
  left: 80px;
  top: 220px;
  border: 3px solid rgb(136, 136, 136);
  cursor: pointer;
}
.island:hover {
  border: 2px solid rgb(14, 116, 233);
}
.island.island_other {
  width: 180px;
  background-position: 40% 10%;
  background-size: 150%;
  background-image: url("../assets/img/taiwan_island2.png");
  top: 100px;
}
.content {
  width: 400px;
  min-width: 350px;
  /* border: 1px solid red; */
  margin: 0px auto;
}
.content.content_second {
  width: 600px;
}

.content .message {
  margin: 15px auto;
  text-align: center;
  width: 60%;
}
.content .slider {
  /* max-width: 500px; */
}

.col50 {
  filter: drop-shadow(2px 2px 1px rgba(0, 0, 0, 0.7));
  position: relative;
  margin: 0px auto;
  height: 300px;
}
.col50 .imgBx {
  height: 100%;
  position: relative;
  width: 100%;
}
.imgBx img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.titleText {
  color: rgb(70, 134, 255);
  font-size: 1.5em;
  font-weight: 1000;
  /* border: 1px solid red; */
  text-align: center;
}
.chageButton {
  position: fixed;
  right: 20px;
  bottom: 20px;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  border: 0px;
  background-image: url("../assets/img/sun.png");
  background-position: center;
  background-size: 60px;
  box-shadow: 0px 5px 7px rgba(0, 0, 0, 0.268);
}
.goBackButton {
  position: fixed;
  right: 20px;
  bottom: 20px;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  border: 0px;
  background-image: url("../assets/img/back.png");
  background-position: center;
  background-size: 45px;
  box-shadow: 0px 5px 7px rgba(0, 0, 0, 0.268);
}
.tableContain {
  display: flex;
  margin: 0px auto;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 10px;
  align-items: center;
}
.tableContaininner {
  width: 130px;
  margin: 10px 5px;
  box-shadow: #8c8c8cad 1px 1 px;
  background: #ffffffaf;
  border: 1px solid #aaa;
  border-radius: 10px;
}
.tableContaininner:nth-child(1) {
  background: #add3ff9d;
  border: 2px solid rgba(30, 39, 95, 0.63);
}
.weatherInf {
  width: 150px;
  /* text-align: center; */
  font-size: 12px;
  padding: 5px;

  /* z-index: 1000; */
}
.weatherInf p {
  margin: 0;
}
.weatherInf p:nth-child(1) {
  font-weight: 700;
}
/* SUNNY */
.sunny {
  /* animation: cloudy 5s ease-in-out infinite; */
  height: 30px;
  width: 30px;
  /* margin-left: 10px; */
  top: -32px;
  left: -5px;
  /* margin-bop: -20px; */
  position: relative;
  /* z-index: -1; */
}
.sunny:before {
  content: "";
  height: 110px;
  width: 20px;
  opacity: 1;
  position: absolute;
  bottom: 0px;
  left: 0px;
  transform: rotate(90deg);
}
.sunny:after {
  background: #ffee44;
  border-radius: 50%;
  box-shadow: rgba(255, 255, 0, 0.2) 0 0 0 15px;
  content: "";
  height: 50px;
  width: 50px;
  position: absolute;
  left: -10px;
  top: 20px;
}
/* CLOUDY */
.cloudy {
  /* animation: cloudy 5s ease-in-out infinite; */
  background: #edededec;
  border-radius: 50%;
  box-shadow: #edededec 35px -15px 0 0px, #edededec 15px -20px 0 2px,
    #edededec 18px 0px 0 0px, #edededec 35px 2px 0 -2px, #edededec 50px 0px 0 0px;
  height: 30px;
  width: 30px;
  left: -10px;
  position: relative;
  /* z-index: -1; */
  /* background: rgba(255, 251, 206, 0.597); */
}
.cloudy.sunCloudy {
  background: #ebeaea;
  box-shadow: #ebeaea 35px -15px 0 0px, #ebeaea 15px -20px 0 2px, #ebeaea 18px 0px 0 0px,
    #ebeaea 35px 2px 0 -2px, #ebeaea 50px 0px 0 0px, #ffee44 50px -20px 0 0px;
}
.cloudy.blackCloudy {
  background: #afafaf;
  box-shadow: #afafaf 35px -15px 0 0px, #afafaf 15px -20px 0 2px, #afafaf 18px 0px 0 0px,
    #afafaf 35px 2px 0 -2px, #afafaf 50px 0px 0 0px;
}
@keyframes cloudy {
  50% {
    transform: translateY(-10px);
  }
}

/* RAINY */
.rainy {
  background: #999999;
  border-radius: 50%;
  box-shadow: #999999 35px -15px 0 0px, #999999 15px -20px 0 2px, #999999 18px 0px 0 0px,
    #999999 35px 2px 0 -2px, #999999 50px 0px 0 0px;
  display: block;
  height: 30px;
  width: 30px;
  left: -10px;
  position: relative;
}
.rainy:before {
  animation: rainy_rain 0.7s infinite linear;
  content: "";
  background: #cccccc;
  border-radius: 50%;
  display: block;
  height: 3px;
  width: 3px;
  opacity: 0.3;
  transform: scale(0.9);
}
@keyframes rainy {
  50% {
    transform: translateY(-10px);
  }
}
@keyframes rainy_rain {
  0% {
    box-shadow: rgba(0, 0, 0, 0) 30px 30px, rgba(0, 0, 0, 0) 40px 40px, #000 50px 75px,
      #000 55px 50px, #000 70px 100px, #000 80px 95px, #000 110px 45px, #000 90px 35px;
  }
  25% {
    box-shadow: #000 30px 45px, #000 40px 60px, #000 50px 90px, #000 55px 65px,
      rgba(0, 0, 0, 0) 70px 120px, rgba(0, 0, 0, 0) 80px 120px, #000 110px 70px,
      #000 90px 60px;
  }
  26% {
    box-shadow: #000 30px 45px, #000 40px 60px, #000 50px 90px, #000 55px 65px,
      rgba(0, 0, 0, 0) 70px 40px, rgba(0, 0, 0, 0) 80px 20px, #000 110px 70px,
      #000 90px 60px;
  }
  50% {
    box-shadow: #000 30px 70px, #000 40px 80px, rgba(0, 0, 0, 0) 50px 100px,
      #000 55px 80px, #000 70px 60px, #000 80px 45px, #000 110px 95px, #000 90px 85px;
  }
  51% {
    box-shadow: #000 30px 70px, #000 40px 80px, rgba(0, 0, 0, 0) 50px 45px, #000 55px 80px,
      #000 70px 60px, #000 80px 45px, #000 110px 95px, #000 90px 85px;
  }
  75% {
    box-shadow: #000 30px 95px, #000 40px 100px, #000 50px 60px,
      rgba(0, 0, 0, 0) 55px 95px, #000 70px 80px, #000 80px 70px,
      rgba(0, 0, 0, 0) 110px 120px, rgba(0, 0, 0, 0) 90px 110px;
  }
  76% {
    box-shadow: #000 30px 95px, #000 40px 100px, #000 50px 60px,
      rgba(0, 0, 0, 0) 55px 35px, #000 70px 80px, #000 80px 70px,
      rgba(0, 0, 0, 0) 110px 25px, rgba(0, 0, 0, 0) 90px 15px;
  }
  100% {
    box-shadow: rgba(0, 0, 0, 0) 30px 120px, rgba(0, 0, 0, 0) 40px 120px, #000 50px 75px,
      #000 55px 50px, #000 70px 100px, #000 80px 95px, #000 110px 45px, #000 90px 35px;
  }
}
@media (max-width: 768px) {
  .content {
    width: 350px;
  }
  .island {
    left: 20px;
  }
  .message h1 {
    font-size: 30px;
  }
  .tableContaininner {
    width: 160px;
    margin: 10px auto;
  }
  .weatherInf {
    font-size: 16px;
  }
}
</style>
